.watermark {
  height: 3rem;
  position: absolute;
  left: 500px;
  top: calc(70px + 40vh);
  opacity: 0.3;
  z-index: 200;
}

.watermark .logoImage {
  height: inherit;
  width: auto;
}

@media screen and (max-width: 860px) {
  .watermark {
    height: 2rem;
    left: auto;
    right: 20px;
    top: calc(70px + 15vh)
  }
}
